$color-gray: #777777;
$color-brown-light: #BA8554;
$color-brown-darck: #A14F03;
$color-black: #000000;
$color-white: #ffffff;

$box-shadow: (0px 4px 4px rgba(0, 0, 0, 0.3));
$box-shadow-active: (0px 4px 4px rgba(0, 0, 0, 0.1));

@mixin style-link($color) {
    display: inline-block;
    text-transform: uppercase;
    text-decoration: none;
    color: $color; }


*, ::after, ::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box; }

html {
    font-size: 62.5%; }

body {
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
    color: $color-gray;
    font-size: 1.6rem;
    letter-spacing: .2rem;
    padding: 5rem;
    background-color: $color-white; }

.container {
    background-color: #F7F7F7; }

.header {
    position: relative;
    height: 95vh;
    background: linear-gradient(90deg, rgba($color-brown-light, 0.7) 0.03%, rgba($color-black, 0.7) 99.94%), url(../img/header-background.jpg) center / cover no-repeat;
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 80%);
    &__logo {
        position: absolute;
        left: 5rem;
        top: 5rem;
        height: 7rem;
        width: 5.6rem; }
    &__box {
        position: absolute;
        top: 40%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        text-align: center; } }
.title-first {
    color: $color-white;
    font-family: 'Roboto';
    &__main {
        display: block;
        font-weight: 800;
        font-size: 5rem;
        line-height: 6rem;
        letter-spacing: 1rem;
        color: $color-white;
        text-transform: uppercase;
        margin-bottom: 1rem;
        animation: leftToRight;
        animation-duration: 1.7s;
        animation-delay: 1s;
        animation-timing-function: ease;
        backface-visibility: hidden; }

    &__secondary {
        display: block;
        font-weight: 300;
        font-size: 4rem;
        line-height: 4.8rem;
        letter-spacing: .2rem;
        animation-name: RightToleft;
        animation-duration: 1.7s;
        animation-delay: 1s;
        animation-timing-function: ease;
        backface-visibility: hidden;
        margin-bottom: 5rem; } }

.btn {
    &:link,&:visited {
        @include style-link($color-gray);
        padding: 1.5rem 4rem;
        background: $color-white;
        border-radius: 5rem;

        transition: all .3s;
        position: relative;
        font-size: 2rem; }
    &:hover {
        transform: translateY(-.4rem);
        box-shadow: $box-shadow;
        &::after {
            opacity: 0;
            transform: scaleX(1.5) scaleY(1.7); } }
    &:active {
        transform: translateY(-.2rem);
        box-shadow: $box-shadow-active; }
    &::after {
        content: "";
        background: $color-white;
        width: 100%;
        height: 100%;
        border-radius: 5rem;
        display: inline-block;
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        transition: all .4s; }
    &_animated {
        animation: BtnToUp .5s ease-out .7s;
        animation-fill-mode: backwards; } }





@keyframes BtnToUp {
    0% {
        opacity: 0;
        transform: translateY(5rem); }

    100% {
        opacity: 1;
        transform: translateY(0); } }


@keyframes leftToRight {
    0% {
        opacity: 0;
        transform: translateX(-10rem); }
    80% {
        transform: translateX(1rem); }

    100% {
        opacity: 1;
        transform: translateX(0); } }

@keyframes RightToleft {
    0% {
        opacity: 0;
        transform: translateX(10rem); }
    80% {
        transform: translateX(-1rem); }

    100% {
        opacity: 1;
        transform: translateX(0); } }





